@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
  text-align: center;
  padding: 2% 5%;
}

.NumberOfEvents {
  padding: 2vh;

}

.getButton {
  margin-left: 20px;
}

.EventList {
  padding: 2vh;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.App * {
  box-sizing: border-box;
}

h1,
h2 {
  color: #105351;
}

ul {
  padding: 0;
  list-style: none;
}

.EventList>li {
  width: 48%;
  margin: 10px;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.showSuggestions {
  display: block;
}

input[type="text"],
input[type="textbox"] {
  padding: 3px 6px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 250px;
  /* position: absolute; */
  /* z-index: 2; */
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions>li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions>li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions>li:hover {
  background-color: #bcdcdb;
}

.event {
  padding: 10px 10px 35px;
  text-align: center;
  border-radius: 4px;
  /* position: relative; */
  border: 1px solid #6ccdca;


}

