  border-radius: 4px;
  outline: none;
  cursor: pointer;
} */

.show-details,
.hide-details,
.getButton {
  font-size: 1em;
  margin-top: 15px;
  border-radius: 4px;
  padding: 10px 15px;
  cursor: pointer;
  border: 1px solid #0bb5af;
}

.show-details:hover,
.hide-details:hover,
.getButton:hover {
  height: 100%;
  background: #10b1a9;
  color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 2px 2px #10b1a9;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.NumberOfEvents {
  display: flex;
  flex-direction: column;

}

/* .numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
} */

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

/* .Alert {
  position: fixed;
  top: 20px;
} */
/* -------------------------------------- Data Visualisation ----------------------------------------------*/
.data-vis-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.data-vis-wrapper>div {
  width: 48%;
}

.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
  background: #1669f2;
}

.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}

.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}

.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}

@media only screen and (max-width: 992px) {
  .EventList>li {
    width: 98%;
    margin: 1%;
  }

  .data-vis-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
  }

  .data-vis-wrapper>div {
    padding: 2%;
    align-content: center;
    width: 98%;
  }

}

@media only screen and (min-width: 992px) {
  .EventList>li {
    width: 48%;
    margin: 1%;
  }
}